@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@600;900&display=swap);
/* Globals */
body {
  font-family: "Montserrat", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans",
    "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

header,
footer {
  color: white;
}

.container {
  margin-top: 2rem;
  margin-bottom: 2rem;
}

.marg-bot-1 {
  margin-bottom: 1rem;
}

.text-align-center {
  text-align: center;
}

.overflow-hidden {
  overflow: hidden;
}

/* BACKGROUNDS */
.bg-semi {
  background-color: #008299;
}

.bg-dark {
  background-color: #172033;
}

.bg-light {
  background-color: lightgray;
}

.bg-whitish {
  background-color: whitesmoke;
}

/* HEADING  */
header {
  margin: 0;
  padding: 2rem;
}

.logo + p {
  color: #eff5fb;
}

.logo a {
  font-family: "Montserrat", sans-serif;
  font-weight: 900;
  font-size: 3rem;
  color: #eff5fb;
  font-style: normal;
  transition: 0.6s;
}

.logo a:hover {
  color: #172033;
  font-style: italic;
}

.search-bar {
  align-self: center;
  padding-top: 1rem;
}

/* ALBUM LIST CARD */
.album-card-img {
  margin: auto;
  background-size: cover;
}

.card-grow {
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
  transition: transform 0.3s ease-out, -webkit-transform 0.3s ease-out;
}

.card-grow:hover,
.card-grow:focus {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}

.album-card-text {
  padding: 1.5rem 1rem;
  text-align: center;
}

a .album-card-text p:hover {
  color: #008299;
  size: 2rem;
}

.parental-advisory-img {
  max-width: 44px;
  vertical-align: text-bottom;
}

/* Artist Detail Page */
.artist-page-card {
  margin: 1rem 0;
}

.artist-page-content {
  padding: 0 1.2rem;
}

.audio-player {
  background-color: #f0f3f4;
  margin: 0.6rem 0 0 0;
  border: 1px #172033 dotted;
}

/* FOOTER */
footer {
  margin: 0;
  padding: 2.5rem;
  text-align: center;
}

footer a {
  color: lightgray;
}

footer a:hover {
  color: #008299;
}

